.shop {
    margin-top: 40px;
}

.gender {
    width: 55px;
}

.gender__female,
.gender__male {
    width: 50px;
    height: 32px;
    background: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.gender__female {
    margin-top: 16px;
}

.category__item {
     
}

.category__item-link {
    display: block;
    background: #FFFFFF;
    border: 2px solid #939393;
    border-radius: 5px;
    padding: 2px 35px;
     width: 140px;
     height: 80px;
     margin-left: 10px;
}


.category-add {
    padding: 31px 17px;
    background: #FFFFFF;
    border: none;
    border-radius: 5px;
    margin-left: 10px;
}

.add-mini {
    padding: 16px 8px;
}

.filter-mini {
    margin-top: 29px;
}

.category__mini {
    margin-top: 26px;
    margin-left: 50px;

    &-item {
        margin-left: 10px;
    }
}

.cart__block {
    width: 194px;
    height: 170px;
    padding: 10px;
    background: #fff;
    border-radius: 5px;
    margin: 5px;
}

.cart__list {
    display: flex;
    flex-wrap: wrap;
}

.cart__block-text {
    margin-top: 10px;
}