.search {
    background: #fff;
    padding: 15px 0;
    width: 100%;

    &__wrapper {
        display: flex;
        margin: 0 auto;
        justify-content: center;
    }
}

.form__search {
    width: 378px;
    height: 40px;
    background: #E8EAEB;
    border: none;
    border-radius: 25px ;
    padding: 10px 20px;
    position: relative;
    top: 0;
    left: 0;
    color: #BABABA;

}

.form__search-btn {
    width: 32px;
    height: 32px;
    border: none;
    background: #0008C1;
    border-radius: 50%;
    position: absolute;
    right: 780px;
    top: 19px;
    cursor:pointer
}
